"use client"

import { useState } from "react"
import { Sidebar } from "@/components/sidebar"
import { AISidebar } from "@/components/ai-sidebar"
import { ResumenTab } from "@/components/tabs/resumen-tab"
import { RentabilidadTab } from "@/components/tabs/rentabilidad-tab"
import { CombustibleTab } from "@/components/tabs/combustible-tab"
import { FlotaTab } from "@/components/tabs/flota-tab"
import { CobranzasTab } from "@/components/tabs/cobranzas-tab"

export type TabType = "resumen" | "rentabilidad" | "combustible" | "flota" | "cobranzas" | "configuracion" | "cuenta"

export default function Dashboard() {
  const [activeTab, setActiveTab] = useState<TabType>("resumen")
  const [isAISidebarOpen, setIsAISidebarOpen] = useState(false)

  const renderTab = () => {
    switch (activeTab) {
      case "resumen":
        return <ResumenTab />
      case "rentabilidad":
        return <RentabilidadTab />
      // ... (el resto de tus casos quedan igual)
      default:
        return <ResumenTab />
    }
  }

  return (
    // üëá 1. El contenedor principal tiene padding (p-3) para separar el contenido de los bordes de la pantalla
    <div className="flex h-screen bg-[#18181b] p-3 overflow-hidden">
      
      <Sidebar activeTab={activeTab} onTabChange={setActiveTab} />
      
      {/* üëá 2. Aqu√≠ est√° la GRAN tarjeta: bg-slate-50, ml-2 (margen izquierdo) y bordes muy curvos */}
      <main className="flex-1 bg-slate-50 rounded-[2.5rem] p-8 overflow-y-auto shadow-2xl ml-2">
        {renderTab()}
      </main>
      
      <AISidebar isOpen={isAISidebarOpen} onToggle={() => setIsAISidebarOpen(!isAISidebarOpen)} />
    </div>
  )
}